/* 공통 스타일링 */
html {
    background-color: #eeeeee;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
    color: black;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
        sans-serif;
}
.container {
    display: block;
    margin-left: auto;
    margin-right: auto;
}
button {
    width: 100px;
    height: 40px;
    display: inline-block;
    border: none;
    background-color: darkgray;
    border-radius: 5px;
    color: white;
    transition: all 300ms ease;
}

.navbar {
    height: 72px;
    display: flex;
    justify-content: space-between;
    padding: 0px 15px;
    font-size: 24px;
    background-color: white;
}

.home-tab {
    line-height: 72px;
}
.home-logo {
    width: 50px;
    vertical-align: middle;
    transition: all 300ms ease;
}
.home-logo:hover {
    transform: scale(1.2);
}

.nav-menu {
    margin-right: 100px;
    line-height: 72px;
    transition: all 300ms ease;
}

.nav-menu:hover {
    color: cornflowerblue;
}

.login-tab {
    line-height: 60px;
}

/*  본문   */

.start-page {
    padding: 2%;
    margin: 70px 50px;
    /* background-color: white; */
    display: flex;
    justify-content: space-evenly;
}

.start-page-left {
    width: 40%;
    float: left;
    /* background-color: aqua; */
}
.group-greeting {
    font-size: 50px;
}
.greeting {
    margin-top: 10%;
    text-align: left;
    font-size: 32px;
}
.start-page-right {
    width: 40%;
    float: right;
    /* background-color: salmon; */
}

.group-img {
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

/* profile */
.profile-section {
    padding: 1%;
}
.greeting {
    text-align: center;
}
.profile-description {
