    height: 15px;
    border-radius: 7px;
    border: none;
    background-color: grey;
}
.profile-btn:hover {
    background-color: black;
}

/* album page */
#img-section {
    margin: 50px 20px;
    background-color: white;
}

.img-page-header {
    margin: 20px;
    display: flex;
    justify-content: space-between;
}
.page-name {
    font-size: 70px;
    text-align: center;
}
.camera-icon {
    width: 70px;
    height: 150px;
    position: relative;
    top: 25px;
}
.imgSection {
    display: flex;
    flex-wrap: wrap;
}
.img-link {
    width: 23%;
    margin: 1%;
}
.camera-btn-a {
    display: block;
    margin: 0 4%;
}

.photo {
    height: 23vw;
    object-fit: fill;
    width: 100%;
    margin: 1%;
    transition: all 300ms ease;
}
.photo:hover {
    transform: scale(1.1);
}
/* postimg page */

.img-upload-section {
