    padding: 50px 20px;
    background-color: white;
}
.img-title-input-box {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}
.img-title-input {
    background-color: #eeeeee;
    font-size: 30px;
    border: none;
    width: 100%;
    height: 40px;
}
.img-description-input {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
}
.upload-img {
    text-align: center;
    width: 34%;
    height: 34vh;
    background-color: #eeeeee;
    background-size: cover;
}
.img-description {
    width: 63%;
    height: 34vh;
    background-color: #eeeeee;
    font-size: 26px;
    border: none;
}
.img-button-wrap {
    margin-top: 40px;
    display: flex;
    justify-content: space-around;
}
.replace-input {
    width: 100px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    display: inline-block;
    border: none;
    background-color: darkgray;
    border-radius: 5px;
    color: white;
    transition: all 300ms ease;
}
.upload-btn {
    width: 100px;
    height: 40px;
    display: inline-block;
    border: none;
    background-color: black;
    border-radius: 5px;
    color: white;
    transition: all 300ms ease;
}
.upload-btn:hover {
    transform: scale(1.1);
}

/* loginpage */

.login-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-55%, -45%);
    width: 400px;
    height: 600px;
    background: white;
    border-radius: 10px;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}
.login-box h1 {
    text-align: center;
    padding: 20px 0;
    border-bottom: 1px solid silver;
}
.login-box form {
    padding: 0 40px;
    box-sizing: border-box;
}
form .txt-field {
    position: relative;
    border-bottom: 2px solid #adadad;
    margin: 30px 0;
}
.txt-field input {
    width: 100%;
    padding: 0 5px;
    height: 50px;
    font-size: 16px;
    border: none;
    background: none;
    outline: none;
}
.txt-field label {
    position: absolute;
    top: 50%;
    left: 5px;
    color: #adadad;
    transform: translateY(-50%);
    font-size: 16px;
    pointer-events: none;
    transition: 0.5s;
}

.txt_field input:focus ~ label,
.txt_field input:valid ~ label {
    top: -5px;
    color: #959595;
}
.txt_field input:focus ~ span::before,
.txt_field input:valid ~ span::before {
    width: 100%;
}
.pass {
    margin: -5px 0 20px 5px;
    text-align: right;
    color: #a6a6a6;
    cursor: pointer;
}
.pass:hover {
    text-decoration: underline;
}
input[type="submit"] {
    width: 100%;
    height: 50px;
    border: 1px solid;
    background: #959595;
    border-radius: 25px;
    font-size: 18px;
    color: #e9f4fb;
    font-weight: 700;
    cursor: pointer;
    outline: none;
}
input[type="submit"]:hover {
    border-color: #959595;
    transition: 0.1s;
}
.signup-link {
    margin: 30px 0;
    text-align: center;
    font-size: 16px;
    color: #666666;
}
.signup-link a {
    color: #959595;
    text-decoration: none;
}
.signup-link a:hover {
    text-decoration: underline;
}

