body {
    width: 100%;
    height: 100%;
    margin: 0px;
    background-color: #ececec;
    min-width: 800px;
}

/* Header Component */
.header {
    width: 100%;
    height: 5rem;
    background-color: white;
}

.header nav {
    height: 100%;
}

.header ul {
    height: 100%;
    width: 100%;
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: space-evenly;
}

.header li {
    margin: 0 1rem;
    width: 10rem;
    text-align: center;
}

.header a {
    display: inline-block;
    text-decoration: none;
    font-size: 24px;
    color: black;
}

.header .item:hover {
    transition-duration: 0.5s;
    transform: scale(1.2);
    color: #95bcf0;
}

.header .item.active {
    padding-bottom: 0.25rem;
    transform: scale(1.2);
    font-weight: 500;
}

/* team container */
.team {
    padding: 0vh 5vw;
}

.member_top {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.profile-team {
    /* text-align: center; */
    padding-left: 10vw;
    font-size: 94px;
    font-weight: 200;
    margin: 40px 0;
}

/* content */
.member_content {
    display: flex;
    min-height: 50vh;
}

.member_content img {
    width: 600px;
    height: 600px;
    object-fit: cover;
}

.member_about {
    height: 100%;
    width: 100%;
    margin: auto;

    padding-left: 5vw;
}

.member_about h2 {
    text-align: center;
    font-size: 72px;
    font-weight: 300;

    margin: 0px;
    padding-top: 5vh;
}

p {
    /* display: inline-block; */
    text-align: center;

    width: 100%;
    font-size: 24px;
    line-height: 4vh;

    margin-top: 5vh;
}

@media (max-width: 1000px) {
    .member_content {
        flex-direction: column;
    }

    .member_about {
        padding-left: 0px;
    }
}
